header.row {
	margin: 0;
}

pre {
	white-space: -moz-pre-wrap;
	white-space: -o-pre-wrap;
	white-space: -pre-wrap;
	white-space: pre-wrap;
	word-wrap: break-word;
}

section.row {
	margin-bottom: 45px;
}

.box-copy > h4 {
	margin-bottom: 15px;
	margin-top: 20px;
}

.page-title {
	display: inline-block;
	font-weight: bold;
	margin-bottom: 25px;

	small {
		display: block;
	}
}

.clay-site-row-spacer,
.lx-site-row-spacer,
.row-spacing {
	margin-bottom: 20px;
}

#headerTopBar {
	background: #0058BA url(../images/back_decoration_nav.png) no-repeat 100% 0;
	position: relative;

	a {
		color: #FFF;
	}

	.container {
		position: relative;
	}

	.title {
		float: left;
		margin-bottom: 14px;
		margin-top: 14px;

		@include media-breakpoint-up(md) { // min-width 768px
			margin-left: 18px;
		}

		.lexicon-version {
			font-size: 60%;
			line-height: 16px;
			vertical-align: middle;
		}
	}

	.navbar-toggle {
		float: left;
		margin-left: -10px;

		.icon-bar {
			background-color: #FFF;
		}
	}
}

#siteConfig {
	position: absolute;
	right: 15px;
	top: 50%;
	-ms-transform: translateY(-50%);
	transform: translateY(-50%);
	z-index: 5;

	.dropdown-menu {
		width: 240px;

		a {
			color: initial;
		}
	}
}

#skipTo {
	left: 15px;
	min-width: 170px;
	position: absolute;
	top: 50%;
	-ms-transform: translateY(-50%);
	transform: translateY(-50%);
	z-index: -1;

	&:focus {
		z-index: 1;
	}
}

.animated-toggle.navbar-toggle .icon-bar {
	position: relative;
	top: 0;
	transition-delay: 0s 0s 0.2s 0s;
	transition-duration: 0.2s;
	transition-property: background, opacity, top, transform;
	transition-timing-function: cubic-bezier(0.785, 0.135, 0.15, 0.86);
}

.main-nav-open {
	.animated-toggle.navbar-toggle {
		.icon-bar:nth-of-type(even) {
			top: 6px;
			-ms-transform: rotate(45deg);
			transform: rotate(45deg);
			transition-delay: 0s 0s 0s 0.2s;
		}

		.icon-bar:nth-of-type(4) {
			top: -6px;
			-ms-transform: rotate(-45deg);
			transform: rotate(-45deg);
		}

		.icon-bar:nth-of-type(odd) {
			background-color: transparent;
			opacity: 0;
		}
	}
}

#imageShapes img {
	display: block;
	max-height: 150px;
	max-width: 150px;
	width: 100%;
}

.hljs {
	background-color: transparent;
}

.lx-site-toggle-code,
.uxgl-toggle-code {
	clear: both;
	padding-bottom: 5px;

	+ .collapse,
	+ .collapsing {
		pre {
			margin-bottom: 0;
		}
	}
}

.lx-site-has-prev-margin-sm {
	margin-top: -7px;
}

.lx-site-has-prev-margin {
	margin-top: -17px;
}

// Alerts Page

.uxgl-alerts {
	.alert {
		margin-bottom: 5px;
	}

	.uxgl-toggle-code ~ .alert {
		margin-top: 10px;
	}
}

#alertNotifications {
	.lexicon-site-display-container {
		@include media-breakpoint-up(md) { // min-width 768px
			float: left;
			margin-right: 15px;
			min-height: 250px;
		}
	}
}

// Best Practices Page

#bestPractices tr th {
	padding-top: 20px;
}

// Blog Actions Page

body.fixed {
	bottom: 0;
	left: 0;
	position: fixed;
	right: 0;
	top: 0;
}

#blogActions {
	[data-target="#imageSelectorModal"] {
		margin-bottom: 15px;
		margin-top: 15px;
	}

	.management-bar {
		margin-bottom: 0;
	}

	.tabular-list-group .list-group-item .list-group-item-content:first-child,
	.tabular-list-group .list-group-item .list-group-item-field:first-child {
		@include media-breakpoint-down(sm) { // min-width 767px
			display: none;
		}
	}
}

.uxgl-fullscreen-fixed {
	background-color: #F5F8FA;
	bottom: 0;
	left: 0;
	overflow: auto;
	position: fixed;
	right: 0;
	top: 0;
	z-index: 10;
}

.uxgl-fullscreen-fixed.row {
	margin-left: 0;
	margin-right: 0;
}

.uxgl-fullscreen-fixed.row > .col-md-12 {
	padding-left: 0;
	padding-right: 0;
}

// Blog Appearance Page

#blogAppearance {
}

.blog-appearance-current-theme {
	background-color: #FFF;
	box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.2);
	min-height: 400px;
	width: 100%;
}

.blog-appearance-current-theme-heading {
	border-bottom: 1px solid #DDD;
	margin-bottom: 15px;
	padding: 15px 0 10px;

	h4 {
		float: left;
		line-height: 26px;
		margin: 0;
	}

	.help-icon {
		float: right;
	}
}

.blog-appearance-left-column {
	img {
		margin-bottom: 15px;
	}
}

.blog-appearance-right-column {
	h4 {
		margin-top: 31px;
	}
}

// Buttons Page

.lx-site-btn-action-column {
	float: left;
	width: 100px;
}

// Cards Page

.clay-site-vertical-card {
	.card-header, .card-footer {
		background: #EEE;
		padding-bottom: 5px;
		padding-top: 5px;
	}

	.card-section {
		padding-bottom: 20px;
		padding-top: 20px;
	}
}

.clay-site-horizontal-card {
	.card-row {
		height: 100px;

		.flex-col {
			text-align: center;

			&:nth-child(odd) {
				background-color: #f7f7f7;
				color: #7c7c85;
			}
		}
	}
}

// Control Menu Page

#controlMenuPage .control-menu {
	position: relative;
}

// Documents and Media Example Page

#documentsAndMediaExample {
	.card-horizontal {
		.card-col-field {
			text-align: center;
		}

		.icon-folder-close-alt {
			background-color: #F1F5FA;
			font-size: 28px;
			height: 60px;
			line-height: 60px;
			width: 60px;
		}
	}
}

#documentsAndMediaRightSidenav {
	background-color: #F5F8FA;

	.breadcrumb {
		margin-bottom: 15px;
		margin-top: 10px;
	}

	.card,
	.card-horizontal {
		background-color: #FFF;
	}

	> .sidenav-content {
		padding-top: 1px;
	}

	.pagination-bar {
		clear: both;
	}
}

@include media-breakpoint-up(md) { // min-width 768px
	#documentsAndMediaSidenav .container-fluid-1280.sidenav-container {
		padding-left: 15px;
	}
}

@media (min-width: 1300px) {
	#documentsAndMediaSidenav.closed .container-fluid-1280.sidenav-container {
		padding-right: 15px;
	}
}

// Dropdowns Page

.clay-site-dropdown-display {
	.dropdown-menu {
		display: block;
		position: relative;
		top: auto;
	}

	.dropdown-wide-container {
		float: left;

		.dropdown-menu {
			display: flex;
		}
	}

	.mega-menu .dropdown-menu {
		float: left;
		max-width: 100%;
	}
}

// Figures Page

.uxgl-figures .figure {
	max-width: 300px;
}

// Extending Cards Page

.card-background-image-demo .aspect-ratio {
	background-color: #000;
}

.usage-section {
	p {
		line-height: 1.6667em;
		margin-bottom: 1.6667em;
	}
}

.usage-heading {
	margin-bottom: 1em;
	margin-top: 0.25em;
}

.extend-cards-page {
	.card {
		max-width: 300px;
	}

	.card-label {
		background: rgb(120, 171, 240);
		color: #FFF;
		display: block;
		float: left;
		font-size: 10px;
		font-weight: 200;
		margin-bottom: 15px;
		margin-left: 10px;
		padding: 2px 10px;
		text-transform: uppercase;
	}

	.card-section table {
		font-weight: 200;
		margin: 15px 0;
		width: 100%;

		td {
			padding: 10px;
			text-align: right;

			&:first-child {
				text-align: left;
			}
		}
	}

	.control-panel {
		.card-header {
			background-color: #F8F8F8;
			box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.3);
			padding: 0;
		}

		.card-row {
			background-color: #F8F8F8;
			box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.3);
			width: 100%;
		}

		.card-column {
			padding: 10px 15px;
		}
	}
}

// Extending Nameplates Page

.uxgl-extending-nameplates {
	.profile-header {
		float: left;
		margin-bottom: 15px;
	}
}

// Grid

.clay-site-grid-width {
	&,
	.clay-site-grid-width-sm,
	.clay-site-grid-width-md,
	.clay-site-grid-width-lg,
	.clay-site-grid-width-xl,
	.clay-site-grid-width-xxl {
		display: none;
	}
}

.clay-site-container-fluid-switcher .btn {
	margin-bottom: 0.5rem;
}

.container-fluid .clay-site-grid-width {
	display: block;
}

.container-fluid-max-sm .clay-site-grid-width-sm,
.container-fluid-max-md .clay-site-grid-width-md,
.container-fluid-max-lg .clay-site-grid-width-lg,
.container-fluid-max-xl .clay-site-grid-width-xl,
.container-fluid-max-xxl .clay-site-grid-width-xxl {
	display: inline;
}

// Icons (Font Awesome Page)

@media (min-width: 321px) {
	.font-awesome-icons-list li {
		float: left;
		width: 50%;
	}
}

@include media-breakpoint-up(md) { // min-width 768px
	.font-awesome-icons-list li, .lexicon-icon-list li {
		float: left;
		padding: 5px;
		width: 33%;
	}

	.glyphicons-list li {
		float: left;
		width: 50%;
	}
}

.lexicon-icon-examples {
	display: block;
	padding: 35px 20px 20px;
	text-align: center;

	.lexicon-icon {
		height: 160px;
		margin: 0 5%;
		transition: all 0.1s ease-in;
		width: 140px;
	}

	.lexicon-icon-asterisk {
		fill-opacity: 0;
		stroke: #078DD8;
		stroke-width: 10px;

		&:hover {
			fill: #078DD8;
			fill-opacity: 1;
			stroke: #000;
			stroke-opacity: 0.3;
			stroke-width: 5px;
		}
	}

	.lexicon-icon-logout {
		fill: #DA3A35;
		fill-opacity: 1;
		stroke: #FFF;
		stroke-width: 55px;

		&:hover {
			fill-opacity: 1;
			stroke: #900;
			stroke-opacity: 0.5;
			stroke-width: 25px;
		}
	}

	@keyframes exclamationHover {
		0% {
			transform: scale(1);
		}

		60% {
			stroke-width: 10px;
			transform: scale(1.5);
		}

		100% {
			transform: scale(1);
		}
	}

	.lexicon-icon-exclamation-full {
		fill: #E3A200;
		stroke: #FFF;
		stroke-opacity: 0.4;
		stroke-width: 40px;

		&:hover {
			animation: exclamationHover 0.25s cubic-bezier(0.175, 0.885, 0.32, 1.275);
			animation-fill-mode: both;
			fill: #EC297B;
		}
	}
}

.modal-open .main-nav-open {
	-webkit-overflow-scrolling: auto;
}

// Nameplates Page

.uxgl-nameplates-page {
	.col-md-2 {
		text-align: center;

		p {
			font-size: 12px;
		}
	}

	.user-icon {
		margin-left: auto;
		margin-right: auto;
	}
}

// Navbars Page

#navbarsPage ~ .row .navbar {
	margin-bottom: 10px;
}

// Icons (Lexicon Page)

