.site-svg-bg {
	background-repeat: no-repeat;
	background-size: contain;
	display: inline-block;
	margin-bottom: 10px;
	margin-right: 15px;
	padding-left: 28px;
}

.site-add-cell {
	background-image: lx-icon(add-cell, $site-lexicon-icon-color);
}

.site-add-column {
	background-image: lx-icon(add-column, $site-lexicon-icon-color);
}

.site-add-row {
	background-image: lx-icon(add-row, $site-lexicon-icon-color);
}

.site-adjust {
	background-image: lx-icon(adjust, $site-lexicon-icon-color);
}

.site-align-center {
	background-image: lx-icon(align-center, $site-lexicon-icon-color);
}

.site-asterisk {
	background-image: lx-icon(asterisk, $site-lexicon-icon-color);
}

.site-times-circle {
	background-image: lx-icon(times-circle, $site-lexicon-icon-color);
